"use client"

import { useState, useEffect } from "react"
import { useParams, useRouter } from "next/navigation"
import { Button } from "@/components/ui/button"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Checkbox } from "@/components/ui/checkbox"
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs"
import { Badge } from "@/components/ui/badge"
import { BackButton } from "@/components/ui/BackButton"
import { Shield, AlertCircle, RefreshCw, Save, User } from "lucide-react"
import { useToast } from "@/components/ui/use-toast"
import usuarioService, { type Usuario } from "@/services/usuarioService"

// Definição das permissões por categoria
const permissoesPorCategoria = {
  Estoque: [
    { id: "ESTOQUE_VISUALIZAR", label: "Visualizar estoque" },
    { id: "ESTOQUE_ADICIONAR", label: "Adicionar produtos" },
    { id: "ESTOQUE_EDITAR", label: "Editar produtos" },
    { id: "ESTOQUE_REMOVER", label: "Remover produtos" },
  ],
  Vendas: [
    { id: "VENDAS_VISUALIZAR", label: "Visualizar vendas" },
    { id: "VENDAS_CRIAR", label: "Criar vendas" },
    { id: "VENDAS_CANCELAR", label: "Cancelar vendas" },
    { id: "VENDAS_RELATORIOS", label: "Relatórios de vendas" },
  ],
  Fila: [
    { id: "FILA_VISUALIZAR", label: "Visualizar fila" },
    { id: "FILA_ADICIONAR", label: "Adicionar clientes" },
    { id: "FILA_REMOVER", label: "Remover clientes" },
    { id: "FILA_GERENCIAR", label: "Gerenciar atendimentos" },
  ],
  Usuários: [
    { id: "USUARIOS_VISUALIZAR", label: "Visualizar usuários" },
    { id: "USUARIOS_ADICIONAR", label: "Adicionar usuários" },
    { id: "USUARIOS_EDITAR", label: "Editar usuários" },
    { id: "USUARIOS_REMOVER", label: "Remover usuários" },
    { id: "USUARIOS_PERMISSOES", label: "Gerenciar permissões" },
  ],
  Sistema: [
    { id: "CONFIGURACOES_VISUALIZAR", label: "Visualizar configurações" },
    { id: "CONFIGURACOES_EDITAR", label: "Editar configurações" },
    { id: "RELATORIOS_VISUALIZAR", label: "Visualizar relatórios" },
    { id: "BACKUP_GERENCIAR", label: "Gerenciar backups" },
  ],
}

export default function PermissoesUsuarioPage() {
  const params = useParams()
  const router = useRouter()
  const { toast } = useToast()
  const userId = Number(params.id)

  const [usuario, setUsuario] = useState<Usuario | null>(null)
  const [permissoes, setPermissoes] = useState<string[]>([])
  const [loading, setLoading] = useState(true)
  const [saving, setSaving] = useState(false)
  const [error, setError] = useState<string | null>(null)
  const [activeTab, setActiveTab] = useState("Estoque")
  const [isLoaded, setIsLoaded] = useState(false) // Flag para evitar carregamentos duplicados

  useEffect(() => {
    // Evitar carregamentos duplicados
    if (!isLoaded) {
      carregarUsuario()
    }
  }, [isLoaded])

  const carregarUsuario = async () => {
    if (isLoaded) return // Evitar carregamentos duplicados

    try {
      setLoading(true)
      setError(null)

      // Timeout para evitar carregamento infinito
      const timeoutPromise = new Promise((_, reject) =>
        setTimeout(() => reject(new Error("Tempo limite excedido ao carregar dados")), 10000),
      )

      // Carregar dados do usuário com timeout
      const userPromise = usuarioService.buscarUsuario(userId)
      const usuarioData = (await Promise.race([userPromise, timeoutPromise])) as Usuario
      setUsuario(usuarioData)

      // Usar as permissões do usuário diretamente se disponíveis
      if (usuarioData.permissoes && usuarioData.permissoes.length > 0) {
        setPermissoes(usuarioData.permissoes)
      } else {
        // Tentar obter permissões específicas apenas se necessário
        try {
          const permPromise = usuarioService.obterPermissoes(userId)
          const permissoesData = (await Promise.race([permPromise, timeoutPromise])) as string[]
          setPermissoes(permissoesData)
        } catch (permError) {
          console.error("Erro ao carregar permissões:", permError)
          // Usar array vazio se não conseguir carregar permissões
          setPermissoes([])

          toast({
            title: "Aviso",
            description: "Não foi possível carregar as permissões específicas.",
            variant: "default",
          })
        }
      }

      setIsLoaded(true) // Marcar como carregado para evitar requisições duplicadas
    } catch (error: any) {
      console.error("Erro ao carregar usuário:", error)
      setError(error.message || "Não foi possível carregar os dados do usuário.")
      toast({
        title: "Erro",
        description: error.message || "Não foi possível carregar os dados do usuário.",
        variant: "destructive",
      })
    } finally {
      setLoading(false)
    }
  }

  const handlePermissaoChange = (permissaoId: string, checked: boolean) => {
    if (checked) {
      setPermissoes((prev) => [...prev, permissaoId])
    } else {
      setPermissoes((prev) => prev.filter((id) => id !== permissaoId))
    }
  }

  const salvarPermissoes = async () => {
    try {
      setSaving(true)
      await usuarioService.atualizarPermissoes(userId, permissoes)
      toast({
        title: "Sucesso",
        description: "Permissões atualizadas com sucesso.",
        variant: "default",
      })
      router.push("/usuarios")
    } catch (error: any) {
      console.error("Erro ao salvar permissões:", error)
      toast({
        title: "Erro",
        description: error.message || "Não foi possível salvar as permissões.",
        variant: "destructive",
      })
    } finally {
      setSaving(false)
    }
  }

  // Função auxiliar para verificar se o usuário está ativo
  const isUsuarioAtivo = (usuario: Usuario): boolean => {
    // Verificar primeiro a propriedade ativo se existir
    if (typeof usuario.ativo === "boolean") {
      return usuario.ativo
    }
    // Caso contrário, verificar o status
    return usuario.status === "Ativo"
  }

  // Função para tentar carregar novamente
  const handleRetry = () => {
    setIsLoaded(false) // Resetar flag para permitir novo carregamento
    carregarUsuario()
  }

  if (loading) {
    return (
      <div className="container py-6 flex items-center justify-center h-[50vh]">
        <div className="text-center">
          <div className="animate-spin h-8 w-8 border-4 border-primary border-t-transparent rounded-full mx-auto mb-4"></div>
          <p>Carregando dados do usuário...</p>
          <Button onClick={() => router.push("/usuarios")} variant="outline" className="mt-4">
            Cancelar
          </Button>
        </div>
      </div>
    )
  }

  const isAdmin = usuario?.perfil === "Administrador" || usuario?.perfil === "ADMIN"

  return (
    <div className="container py-6">
      <div className="flex items-center justify-between mb-6">
        <div className="flex items-center gap-4">
          <BackButton />
          <h1 className="text-3xl font-bold">Gerenciar Permissões</h1>
        </div>
        <Button onClick={salvarPermissoes} disabled={saving || isAdmin}>
          <Save className="mr-2 h-4 w-4" />
          {saving ? "Salvando..." : "Salvar Permissões"}
        </Button>
      </div>

      {error ? (
        <Card className="mb-6 border-destructive">
          <CardContent className="pt-6">
            <div className="flex items-center gap-2 text-destructive">
              <AlertCircle className="h-5 w-5" />
              <p>{error}</p>
            </div>
            <Button onClick={handleRetry} className="mt-4">
              <RefreshCw className="mr-2 h-4 w-4" />
              Tentar novamente
            </Button>
          </CardContent>
        </Card>
      ) : null}

      {usuario && (
        <Card className="mb-6">
          <CardHeader>
            <div className="flex items-center justify-between">
              <div>
                <CardTitle>{usuario.nome}</CardTitle>
                <CardDescription>{usuario.username}</CardDescription>
              </div>
              <div className="flex items-center gap-2">
                {isAdmin ? (
                  <Badge variant="destructive" className="flex items-center gap-1">
                    <Shield className="h-3 w-3" />
                    Administrador
                  </Badge>
                ) : (
                  <Badge variant="outline" className="flex items-center gap-1">
                    <User className="h-3 w-3" />
                    Operador
                  </Badge>
                )}
                {isUsuarioAtivo(usuario) ? (
                  <Badge variant="outline" className="bg-green-500/20 text-green-700 hover:bg-green-500/20">
                    Ativo
                  </Badge>
                ) : (
                  <Badge variant="outline" className="bg-gray-500/20 text-gray-700 hover:bg-gray-500/20">
                    Inativo
                  </Badge>
                )}
              </div>
            </div>
          </CardHeader>
          {isAdmin && (
            <CardContent>
              <div className="bg-yellow-50 border border-yellow-200 rounded-md p-4 text-yellow-800">
                <p className="text-sm font-medium">
                  Administradores têm acesso completo ao sistema. Não é necessário configurar permissões individuais.
                </p>
              </div>
            </CardContent>
          )}
        </Card>
      )}

      {!isAdmin && (
        <Tabs value={activeTab} onValueChange={setActiveTab}>
          <TabsList className="mb-4">
            {Object.keys(permissoesPorCategoria).map((categoria) => (
              <TabsTrigger key={categoria} value={categoria}>
                {categoria}
              </TabsTrigger>
            ))}
          </TabsList>

          {Object.entries(permissoesPorCategoria).map(([categoria, listaPermissoes]) => (
            <TabsContent key={categoria} value={categoria}>
              <Card>
                <CardHeader>
                  <CardTitle>Permissões de {categoria}</CardTitle>
                  <CardDescription>
                    Configure as permissões de {categoria.toLowerCase()} para este usuário.
                  </CardDescription>
                </CardHeader>
                <CardContent>
                  <div className="grid gap-4 md:grid-cols-2">
                    {listaPermissoes.map((permissao) => (
                      <div key={permissao.id} className="flex items-center space-x-2">
                        <Checkbox
                          id={permissao.id}
                          checked={permissoes.includes(permissao.id)}
                          onCheckedChange={(checked) => handlePermissaoChange(permissao.id, checked as boolean)}
                        />
                        <label
                          htmlFor={permissao.id}
                          className="text-sm font-medium leading-none peer-disabled:cursor-not-allowed peer-disabled:opacity-70"
                        >
                          {permissao.label}
                        </label>
                      </div>
                    ))}
                  </div>
                </CardContent>
              </Card>
            </TabsContent>
          ))}
        </Tabs>
      )}
    </div>
  )
}
